<template>
  <view class="home_new_song">
    <uni-nav :title="title" class="newSong_nav" @click.native="toNewSonglist"></uni-nav>
    <uni-song :song="item" v-for="(item, index) in newSong" :key="index"></uni-song>
  </view>
</template>

<script>
import UniNav from "@/components/nav/Uni-Nav";
import UniSong from "@/components/song/Uni-Song";

export default {
  props: {
    newSong: {
      type: Array,
      default() {
        return [];
      }
    },
    title: {
      type: String,
      default: ""
    },
    ids: {
      type: String,
      default: ""
    }
  },
  components: {
    UniNav,
    UniSong
  },
  methods: {
    toNewSonglist() {
      uni.navigateTo({
        url: `/pages/newSonglist/NewSonglist?ids=${this.ids}`,
        animationType: 'slide-in-right',
        animationDuration: 300,
        /*success: res => {
          res.eventChannel.emit('acceptNewSonglist', this.ids);
        }*/
      })
    }
  }
}
</script>

<style scoped lang="scss">
  .home_new_song{
    width: $container-size;
    margin: 0 auto 50rpx;
    .newSong_nav{
      width: 100%;
    }
  }
</style>